
import { defineComponent, nextTick } from 'vue';
import AjaxHelper from '../AjaxHelper/AjaxHelper';

const { $ } = window;

export default defineComponent({
  props: {
    notificationId: String,
    // TODO: shouldn't need this since the title can be specified within
    //       HTML of the node that uses the directive.
    title: String,
    context: String,
    type: String,
    noclear: Boolean,
    toastLength: {
      type: Number,
      default: 12 * 1000,
    },
    style: String,
    animate: Boolean,
    position: Object,
  },
  computed: {
    canClose() {
      if (this.type === 'persistent') {
        // otherwise it is never possible to dismiss the notification
        return false;
      }
      return this.noclear;
    },
    positionStyles() {
      return this.position && {
        position: 'absolute',
        top: this.position.top,
        left: this.position.left,
      };
    },
  },
  emits: ['closed'],
  mounted() {
    const addToastEvent = () => {
      setTimeout(() => {
        this.deleted = true;
      }, this.toastLength);
    };

    if (this.type === 'toast') {
      addToastEvent();
    }

    if (this.style) {
      $(this.$refs.root).css(this.style);
    }
  },
  methods: {
    toastClosed() {
      nextTick(() => {
        this.$emit('closed');
      });
    },
    closeNotification(event: MouseEvent) {
      if (this.canClose && event && event.target) {
        this.deleted = true;

        nextTick(() => {
          this.$emit('closed');
        });
      }

      this.markNotificationAsRead();
    },
    markNotificationAsRead() {
      if (!this.props.notificationId) {
        return;
      }

      AjaxHelper.fetch({ // GET params
        module: 'CoreHome',
        action: 'markNotificationAsRead',
      }, { // POST params
        postParams: { notificationId: this.props.notificationId },
      });
    },
  },
});
